<template name='sunui-autofill'>
	<view>
		<view>
			<input class="sunui-autofill-input" @input="matchVal" :value="iptVal" :placeholder="placeHolder" />
		</view>
		<scroll-view :scroll-y="true">
			<!-- 这里可以DIY文字颜色 -->
			<view v-for="(item,index) in matchSource" :key='index'>
				<view :id="item" @tap="selectedVal" class="sunui-autofill-item">{{item}}</view>
			</view>
			<view v-show="isExist" style="text-align: center;padding: 6% 2%;color: #666;">
				<text v-show="matchSource.length==0">未查找到“{{iptVal}}”,</text>
				<text>共计{{matchNum}}条查询结果</text>
			</view>
		</scroll-view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				iptVal: '',
				isExist: true,
				matchSource: [],
				matchNum: 0
			}
		},
		props: {
			autoArray: {
				type: Array,
				default: function() {
					return [];
				}
			},
			placeHolder: {
				type: String,
				default: `请输入查找的内容`
			}
		},
		name: 'sunui-autofill',
		methods: {
			matchVal(e) {
				let matchStr = e.detail.value;
				let matchSource = [];
				if (matchStr == "") {
					this.matchSource = [];
					this.matchNum = 0;
					this.isExist = true;
					this.iptVal = '';
					return;
				}
				this.autoArray.forEach((e) => {
					if (e.toString().indexOf(matchStr) != -1) {
						matchSource.push(e)
						this.matchNum = matchSource.length;
					} else {
						this.iptVal = matchStr;
						if (matchSource.length == 0) {
							this.matchNum = 0;
						}
					}
				});
				matchSource.length != 0 ? this.matchSource = matchSource : this.matchSource = [];
			},
			selectedVal(e) {
				this.iptVal = e.target.id;
				this.matchSource = [];
				this.matchNum = 0;
				if (this.iptVal) {
					this.isExist = false;
				}
				this.$emit('change', {
					val: this.iptVal
				});
			}
		}
	}
</script>

<style>
	.sunui-autofill-input,
	.sunui-autofill-item {
		border-bottom: 1upx solid #eee;
		padding: 2%;
	}
</style>
